<template>
  <div class="about">
    <h1>{{id ? '' : ''}}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-input v-model="model.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-button size="small" @click="model.items.push({})">
          <i class="el-icon-plus"></i>
        </el-button>
        <el-row type="flex" style="flex-wrap: wrap">
          <el-col v-for="(item, i) in model.items" :key="i" :md="24">
            <el-form-item label="(URl)">
              <el-input v-model="item.url"></el-input>
            </el-form-item>
            <el-form-item label="" style="margin-top:0.5rem;">
              <el-upload
                class="avatar-uploader"
                :action="uploadUrl"
                :headers="getAuthHeaders()"
                :show-file-list="false"
                :on-success="res => $set(item, 'image', res.url)"
              >
                <!--   -->
                <img v-if="item.image" :src="item.image" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>

            <el-form-item>
              <el-button size="small" type="danger" @click="model.items.splice(i, 1)"></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      model: {
        items: []
      }
      // 
    };
  },
  methods: {
    async save() {
      let res;
      if (this.id) {
        // id 
        res = await this.$http.put(`rest/ads/${this.id}`, this.model);
      } else {
        //  1
        res = await this.$http.post("rest/ads", this.model);
      }
      // 
      this.$router.push("/ads/list");
      // 
      this.$message({
        type: "success",
        message: ""
      });
    },
    async fetch() {
      const res = await this.$http.get(`rest/ads/${this.id}`);
      // data
      this.model = Object.assign({}, this.model, res.data);
    }
  },

  created() {
    // id 
    this.id && this.fetch();
  }
};
</script>