<template>
  <div class="about">
    <h1></h1>
    <el-form
      :model="model"
      status-icon
      :rules="rules"
      ref="model"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="">
        <el-input v-model="model.username" :disabled="true"></el-input>
      </el-form-item>

      <el-form-item label="" prop="password">
        <el-input
          type="password"
          v-model="model.password"
          show-password
          autocomplete="off"
        ></el-input>
      </el-form-item>

      <el-form-item label="" prop="checkPass">
        <el-input
          type="password"
          v-model="model.checkPass"
          show-password
          autocomplete="off"
        ></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model.number="model.phone" :disabled="true" />
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="model.status" :label="0"></el-radio>
        <el-radio v-if="status == 0 ? !show : show" v-model="model.status" :label="1"></el-radio>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          :action="uploadUrl"
          :headers="getAuthHeaders()"
          :show-file-list="false"
          :on-success="afterUpload"
        >
          <!--   -->
          <img v-if="model.avatar" :src="model.avatar" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('model')"></el-button>
        <el-button @click="resetForm('model')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data() {
    var validatePass = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      }
      callback();
    };
    var validatePass2 = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      }
      callback();
    };

    return {
      avatar: "",
      show: true,
      status: '',
      model: {
        password: "",
        checkPass: "",
        username: "",
        phone: "",
        status: "",
      },
      rules: {
        password: [
          { required: true, validator: validatePass, trigger: "blur" }
        ],
        checkPass: [
          { required: true, validator: validatePass2, trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.save();
        } else {
          return false;
        }
      });
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

    // 
    afterUpload(res) {
      this.$set(this.model, "avatar", res.url);
    },
    async save() {
      let res;

      if (this.id) {
        // id 
        res = await this.$http.post(`admin_update/${this.id}`, this.model,);
        var err_code = res.data.err_code;
        if (err_code === 2) {
          this.$message({
            type: "error",
            message: ""
          });
        } else if (err_code === 0) {
          this.$message({
            type: "success",
            message: ""
          });
          this.$router.push("/admin_users/list");
        }
      }
      // 
    },

    async fetch() {
      const res = await this.$http.get(`rest/admin_users/${this.id}`);
      // data
      this.model = res.data;
      this.status = localStorage.getItem("status");
    }
  },

  created() {
    // id 
    this.id && this.fetch();
  }
};
</script>