<template>

  <el-container style="height: 100vh;">
   
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)">

      <el-menu router unique-opened :default-active="$route.path">

        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-message"></i>
          </template>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/items/create"></el-menu-item>
            <el-menu-item index="/items/list"></el-menu-item>
          </el-menu-item-group>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/goodses/create"></el-menu-item>
            <el-menu-item index="/goodses/list"></el-menu-item>
          </el-menu-item-group>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/articles/create"></el-menu-item>
            <el-menu-item index="/articles/list"></el-menu-item>
          </el-menu-item-group>
        </el-submenu>

        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-s-cooperation"></i>
          </template>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/ads/create"></el-menu-item>
            <el-menu-item index="/ads/list"></el-menu-item>
          </el-menu-item-group>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/message/list"></el-menu-item>
          </el-menu-item-group>

        </el-submenu>

        <el-submenu index="3">
          <template slot="title">
            <i class="el-icon-setting"></i>
          </template>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/goods_categories/create"></el-menu-item>
            <el-menu-item index="/goods_categories/list"></el-menu-item>
          </el-menu-item-group>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/article_categories/create"></el-menu-item>
            <el-menu-item index="/article_categories/list"></el-menu-item>
          </el-menu-item-group>

          

          <el-menu-item-group>
            <template slot="title">{{ this.status != 1 ? '' : ''}}</template>
            <el-menu-item index="/admin_users/create">{{ this.status != 1 ? '' : ''}}</el-menu-item>
            <el-menu-item index="/admin_users/list">{{ this.status != 1 ? '' : ''}}</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
      </el-menu>
    </el-aside>

    <el-container>
      <el-header style="text-align: right; font-size: 12px">
        <el-dropdown>
          <el-avatar :src="this.avatar"></el-avatar>
          <span>
            {{this.username}}</span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="Add">{{ this.status != 1 ? '' : ''}}</el-dropdown-item>
            <el-dropdown-item @click.native="updated"></el-dropdown-item>
            <el-dropdown-item @click.native="logout"></el-dropdown-item>
            <el-dropdown-item @click.native="Adminexit">{{ this.status != 1 ? '' : ''}}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-header>

      <el-main>
        <router-view :key="$route.path"></router-view>

      </el-main>
    </el-container>
  </el-container>
</template>
<style>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
</style>

<script>
export default {
  data() {
    const item = {
      date: "2016-05-02",
      name: "",
      address: " 1518 "
    };
    return {
      tableData: Array(20).fill(item),
      username:[],
      avatar:[],
      status:[],
      id: []
    };
  },
  methods: {
    Adminexit() {
      localStorage.clear();
      this.$message("");
      this.$router.push("login");
    },
    Add() {
      this.$router.push({ path: "/admin_users/create" });
    },
    updated () {
      this.$router.push(`/admin_users/update/${this.id}`)
    },
    logout() {
     this.remove()
    },
    loadComments() {
      this.username = localStorage.getItem("username");
      this.avatar = localStorage.getItem("avatar");
      this.status = localStorage.getItem("status");
      this.id = localStorage.getItem("id");
    },
    async remove() {
      this.$confirm(`"${this.username}"`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
         await this.$http.delete(`rest/admin_users/${this.id}`);
          this.$message({
            type: "success",
            message: "!"
          });
      this.$router.push({ path: "/login" });
        })
    }
  },

  created() {
    this.loadComments()
  },
};
</script>