<template>
  <div class="about">
    <h1></h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="240"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>

      <el-table-column fixed="right" label="" width="180">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="small"
            @click="$router.push(`/ads/edit/${scope.row._id}`)"
          ></el-button>

          <el-button type="danger" size="small" @click="remove(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      items: []
    };
  },
  methods: {
    async fetch() {
      const res = await this.$http.get("rest/ads");
      this.items = res.data;
    },

    async remove(row) {
      this.$confirm(`"${row.name}"`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
      // 
        .then(async () => {
          // id
          const res = await this.$http.delete(`rest/ads/${row._id}`)
          // 
          this.$message({
            type: "success",
            message: "!"
          });
          this.fetch()
        })
        // 
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  created() {
    // 
    this.fetch();
  }
};
</script>
<style lang="stylus"></style>