<template>
  <div class="about">
    <h1></h1>

    <el-col :span="24">
      <el-button type="primary" :disabled="this.tableChecked.length === 0" @click="batchDelete(tableChecked)" ></el-button>
    </el-col>

    <el-table :data="items"  @selection-change="handleSelectionChange">

      <el-table-column type="selection" width="55"></el-table-column>

      <el-table-column label="" type="index" :index="table_index" show-ov erflow-tooltip width="50"></el-table-column>

      <el-table-column prop="_id" label="ID" width="240"></el-table-column>

      <el-table-column prop="title" label=""></el-table-column>

      

      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{ scope.row.createdAt | date }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="categories[0].name" label=""></el-table-column>


      <el-table-column fixed="right" label="" width="300">

        <!--  -->
        <template slot="header">
          <el-input class="search" v-model="search" prefix-icon="el-icon-search" clearable placeholder="" />
        </template>

        <!--  -->
        <template slot-scope="scope">
          <el-button type="primary" size="small" @click="$router.push(`/articles/edit/${scope.row._id}`)" ></el-button>
          <el-button type="danger" size="small" @click="remove(scope.row)"></el-button>
        </template>

      </el-table-column>

    </el-table>

    <!--  -->

    <el-row :span="24">
      <div class="pagination-list">
        <el-pagination background @current-change="handleCurrentChange" :current-page.sync="paginations.page_index" :page-size="paginations.page_size" :layout="paginations.layout" :total="paginations.total" ></el-pagination>
      </div>
    </el-row>

  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  filters: {
    date(val) {
      return dayjs(val).format("YYYY-MM-DD HH:mm"); // 
    }
  },

  data() {

    return {
      items: [],
      tableChecked: [],
      ids: [],
      allItems: [],
      paginations: {
        page_index: 1, //
        total: 0, //
        page_size: 10, //
        layout: "prev, pager, next"
      },
      search: '', //
    };
  },

  methods: {

    /**
     * 
     */
   table_index(index){
        return (this.paginations.page_index-1) * this.paginations.page_size + index + 1
    },

     /**
     * 
     */
    searchItem() {
        const searchItemdata = this.allItems.filter(data => !this.search || data.title.toLowerCase().includes(this
          .search
          .toLowerCase()))
        this.allItems = searchItemdata
        this.setPaginations()
      },

    setPaginations() {
      this.paginations.total = this.allItems.length;
      this.paginations.page_index = 1;
      this.paginations.page_size = 10;
      this.items = this.allItems.filter((tableitems, index) => {
        return index < this.paginations.page_size;
      });
    },

    /**
     * 
     */
    handleCurrentChange(page) {
      let index = this.paginations.page_size * (page - 1);
      let items_num = this.paginations.page_size * page;
      let tables = [];
      for (let i = index; i < items_num; i++) {
        if (this.allItems[i]) {
          tables.push(this.allItems[i]);
        }
        this.items = tables;
      }
    },

    /**
     * 
     */
    handleSelectionChange(val) {
      this.tableChecked = val;
    },

    /**
     * 
     */
    async fetch() {
      const res = await this.$http.get("rest/articles");
      this.allItems = res.data;
      this.setPaginations();
    },

    /**
     *  tableChecked
     */
    async batchDelete() {
      var ids = this.tableChecked.map(item => item._id).join();
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })

        .then(async () => {
          await this.$http.delete("rest/articles/del/" + ids);
          this.$message({
            type: "success",
            message: "!"
          });
          this.fetch();
        })

        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    /**
     * 
     */
    async remove(row) {
      this.$confirm(`"${row.title}"`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        
        .then(async () => {// 
          await this.$http.delete(`rest/articles/${row._id}`);
          this.$message({
            type: "success",
            message: "!"
          });
          this.fetch();
        })
        
        .catch(() => {// 
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

  },
  created() {
    this.fetch();// 
  },

  watch: {
      search: function (new_v) {
        if (new_v != '') {
          this.searchItem()
        } else {
          this.fetch()
        }
      }
    }
};
</script>
