<template>
  <div class="about">
    <h1>{{id ? '' : ''}}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">

      <el-form-item label="">
        <el-input v-model="model.name" placeholder=""></el-input>
      </el-form-item>
      
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      model: {},
      // 
      parents: []
    };
  },
  methods: {
    async save() {
      let res;
      if (this.id) {
        // id 
        res = await this.$http.put(`rest/article_categories/${this.id}`, this.model);
      } else {
        //  1
        res = await this.$http.post("rest/article_categories", this.model);
      }
      // 
      this.$router.push("/article_categories/list");
      // 
      this.$message({
        type: "success",
        message: ""
      });
    },
    async fetch() {
      const res = await this.$http.get(`rest/article_categories/${this.id}`);
      // data
      this.model = res.data;
    },

  },

  created() {
    // id 
    this.id && this.fetch();
  }
};
</script>