<template>
  <div class="about">
    <h1>{{id ? '' : ''}}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-select v-model="model.categories" multiple>
          <el-option
            v-for="item in article_categories"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="model.title"></el-input>
      </el-form-item>

      <el-form-item label="">
        <vue-editor v-model="model.body" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { VueEditor } from "vue2-editor";

export default {
  props: {
    id: {}
  },
  components: {
    VueEditor
  },
  data() {
    return {
      model: {},
      // 
      article_categories: []
    };
  },
  methods: {
    // 
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      
      const formData = new FormData();
      formData.append("file", file);
      const res = await this.$http.post("upload", formData);
      var err_code = res.data.err_code;
      if (err_code === 2) {
        this.$message({
          type: "error",
          message: ""
        });
      }
      Editor.insertEmbed(cursorLocation, "image", res.data.url);
      resetUploader();
    },
    async save() {
      let res;
      if (this.id) {
        // id 
        res = await this.$http.put(`rest/articles/${this.id}`, this.model);
      } else {
        //  1
        res = await this.$http.post("rest/articles", this.model);
      }
      // 
      this.$router.push("/articles/list");
      // 
      this.$message({
        type: "success",
        message: ""
      });
    },
    async fetch() {
      const res = await this.$http.get(`rest/articles/${this.id}`);
      // data
      this.model = res.data;
    },

    async fetchCatogories() {
      const res = await this.$http.get(`rest/article_categories`);
      // data
      this.article_categories = res.data;
    }
  },

  created() {
    this.fetchCatogories();
    // id 
    this.id && this.fetch();
  }
};
</script>

<style lang="scss">
.ql-snow .ql-picker {
  height: 43px !important;
}
</style>