<template>
  <div class="login-container">
    <el-card header="" class="login-card">
      <el-form :model="model" :rules="rules" ref="model" class="demo-ruleForm">

        <el-form-item label="" prop="username">
          <el-input placeholder="" prefix-icon="el-icon-user"
         v-model="model.username"></el-input>
        </el-form-item>

        <el-form-item label="" prop="password">
          <el-input type="password" prefix-icon="el-icon-unlock" 
          placeholder=""  show-password v-model="model.password" ></el-input>
        </el-form-item>

       <el-form-item>
        <el-button type="primary" @click="submitForm('model')"></el-button>
        <el-button @click="resetForm('model')"></el-button>
      </el-form-item>

      </el-form>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {

    return {
      model: {
        username: "",
        password: ""
      },
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.login();
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

    // 
    async login() {
      const res = await this.$http.post("login", this.model);
      // 
      // sessionStorage.token = res.data.token
      // 
      localStorage.token = res.data.token;
      localStorage.avatar = res.data.avatar;
      localStorage.status = res.data.status;
      localStorage.id = res.data.id;
      localStorage.setItem("username", this.model.username);
      this.$router.push("/");

      this.$message({
        type: "success",
        message: ""
      });
    }
  }
};
</script>

<style>
.login-card {
  width: 25rem;
  margin: 8rem auto;
}
</style>