<template>
  <div class="about">
    <h1>{{id ? '' : ''}}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-tabs type="border-card" value="basic">
        <!--  -->
        <el-tab-pane label="" name="basic">
          <el-form-item label="">
            <el-input v-model="model.name"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="model.spec"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              :headers="getAuthHeaders()"
              :show-file-list="false"
              :on-success="res => $set(model, 'avatar', res.url)"
            >
              <!--   -->
              <img v-if="model.avatar" :src="model.avatar" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>

          <el-form-item label="">
            <el-select v-model="model.categories" multiple>
              <el-option
                v-for="item of goods_categories"
                :key="item._id"
                :value="item._id"
                :label="item.name"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="">
            <el-select v-model="model.items1" multiple>
              <el-option v-for="item of items" :key="item._id" :value="item._id" :label="item.name"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="">
            <el-rate style="margin-top:0.6rem;" :max="9" show-score v-model="model.scores.difficult"></el-rate>
          </el-form-item>

          <el-form-item label="">
            <el-rate style="margin-top:0.6rem;" :max="9" show-score v-model="model.scores.skills"></el-rate>
          </el-form-item>

          <el-form-item label="">
            <el-rate style="margin-top:0.6rem;" :max="9" show-score v-model="model.scores.effect"></el-rate>
          </el-form-item>

          <el-form-item label="">
            <el-rate style="margin-top:0.6rem;" :max="9" show-score v-model="model.scores.utility"></el-rate>
          </el-form-item>

          <el-form-item label="">
            <el-input type="textarea" v-model="model.color"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input type="textarea" v-model="model.feature"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input type="textarea" v-model="model.scope"></el-input>
          </el-form-item>
        </el-tab-pane>
        <!--  -->

        <!--  -->
        <el-tab-pane label="" name="partners">
          <el-button size="small" @click="model.partners.push({})">
            <i class="el-icon-plus"></i>
          </el-button>
          <el-row type="flex" style="flex-wrap: wrap">
            <el-col v-for="(item, i) in model.partners" :key="i" :md="12">
              <el-form-item label="">
                <el-select filterable v-model="item._id">
                  <el-option
                    v-for="item in goodses"
                    :key="item._id"
                    :value="item._id"
                    :label="item.name"
                  ></el-option>
                </el-select>
              </el-form-item>


              <el-form-item label="">
                <el-input v-model="item.description" type="textarea"></el-input>
              </el-form-item>

              <el-form-item>
                <el-button size="small" type="danger" @click="model.partners.splice(i, 1)"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
      </el-tabs>
      <el-form-item style="margin-top:1rem;">
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      categories: [],
      goods_categories: [],
      items: [],
      goodses: [],
      model: {
        name: "",
        avatar: "",
        skills: [],
        partners: [],
        scores: {
          difficult: 0
        }
      }
    };
  },
  methods: {
    async save() {
      let res;
      if (this.id) {
        // id 
        res = await this.$http.put(`rest/goodses/${this.id}`, this.model);
      } else {
        //  1
        res = await this.$http.post("rest/goodses", this.model);
      }
      // 
      this.$router.push("/goodses/list");
      // 
      this.$message({
        type: "success",
        message: ""
      });
    },
    async fetch() {
      const res = await this.$http.get(`rest/goodses/${this.id}`);
      // data
      //   
      this.model = Object.assign({}, this.model, res.data);
    },
    async fetchItems() {
      const res = await this.$http.get(`rest/items`);
      // data
      this.items = res.data;
    },

    async fetchGoodses() {
      const res = await this.$http.get(`rest/goodses`);
      // data
      this.goodses = res.data;
    },

    async fetchCatogories() {
      const res = await this.$http.get(`rest/goods_categories`);
      // data
      this.goods_categories = res.data;
    }
  },

  created() {
    this.fetchCatogories();
    this.fetchGoodses(); //
    this.fetchItems(); 
    // id 
    this.id && this.fetch();
  }
};
</script>
