<template>
  <div class="about">
    <h1></h1>
    <el-table :data="items">
      <el-table-column label="" type="index" show-overflow-tooltip width="50"></el-table-column>
      <el-table-column prop="username" label=""></el-table-column>
      <el-table-column prop="phone" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{ scope.row.status === 0 ? '' : '' }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <div style="width: 100px;border-radius:50%; ">
            <img :src="scope.row.avatar" style="width: 100%;border-radius:50%; " />
          </div>
        </template>
      </el-table-column>
      
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{ scope.row.createdAt | date }}</span>
        </template>
      </el-table-column>

      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          
        
          <el-button
            type="primary"
            size="small"
            v-if="status != 1 ? !show : show"
            @click="$router.push(`/admin_users/update/${scope.row._id}`)"
          ></el-button>

          <el-button
          v-if="status != 1 ? !show : show"
           type="danger" size="small" @click="remove(scope.row)"></el-button>

        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import dayjs from "dayjs";
export default {
  filters: {
    date(val) {
      // 
      return dayjs(val).format("YYYY-MM-DD HH:mm");
    }
  },
  data() {
    return {
      username: [],
      items: [],
      status: '',
      show: true,
      model: {
      }
    };
  },
  methods: {
    async fetch() {
      const res = await this.$http.get("rest/admin_users");
      this.items = res.data;
      this.username = localStorage.getItem("username");
      this.status = localStorage.getItem("status");
    },

    async remove(row) {
      this.$confirm(`"${row.username}"`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        // 
        .then(async () => {
          // id
         await this.$http.delete(`rest/admin_users/${row._id}`);
          // 
          this.$message({
            type: "success",
            message: "!"
          });
          this.fetch();
        })
        // 
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  created() {
    // 
    this.fetch();
  }
};
</script>
<style>
.el-switch {
  margin-right: 10px;
}
</style>